import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import useForm from '../hooks/useForm';
import { Stack } from './primitives/primitives';

const ConfigForm = ({ setConfiguration }) => {
    const { handleChange, handleCheckbox, handleSubmit, values } = useForm(configure, update);
    const navigate = useNavigate();
    const [hardcore, setHardcore] = useState(false);

    function configure(values) {
        setConfiguration(values);
        navigate('/play');
    };

    function update() {
       setHardcore(values.hardcore);
    };

    return (
        <form onSubmit={handleSubmit}>
            <Stack>
            <fieldset onChange={handleChange}>
                <legend>Who goes first?</legend>
                <label htmlFor="becky">Becky</label>
                <input
                    id="becky"
                    name="first_player"
                    type="radio"
                    value="Becky"
                />
                <label htmlFor="paul">Paul</label>
                <input
                    id="paul"
                    name="first_player"
                    type="radio"
                    value="Paul"
                />
            </fieldset>

            <fieldset>
                <label htmlFor="totalRounds">How many rounds?</label>
                <input
                    id="totalRounds"
                    name="total_rounds"
                    type="number"
                    min="3"
                    max="50"
                    step="1"
                    onChange={handleChange}
                    value={values.total_rounds}
                />
            </fieldset>

            <fieldset>
                <label htmlFor="roundsForeplay">How many rounds of foreplay?</label>
                <input
                    id="roundsForeplay"
                    name="rounds_foreplay"
                    type="number"
                    min="0"
                    max="20"
                    step="1"
                    onChange={handleChange}
                    value={values.rounds_foreplay}
                />
            </fieldset>

            <fieldset>
                <label htmlFor="anal">Anal</label>
                <input
                    id="anal"
                    name="anal"
                    type="checkbox"
                    onChange={handleCheckbox}
                    value={values.anal}
                />
            </fieldset>

            <fieldset>
                <label htmlFor="hardcore">Hardcore</label>
                <small>Choking, slapping, and spitting</small>
                <input
                    id="hardcore"
                    name="hardcore"
                    type="checkbox"
                    onChange={handleCheckbox}
                    value={values.hardcore}
                />
            </fieldset>

            {hardcore && (
                <fieldset>
                    <label htmlFor="foreplay">How many hardcore rounds?</label>
                    <input
                    id="roundsHardcore"
                    name="rounds_hardcore"
                    type="number"
                    min="0"
                    max="20"
                    step="1"
                    onChange={handleChange}
                    value={values.rounds_hardcore}
                    />
                </fieldset>
            )}

            <fieldset>
                <label htmlFor="toys">Toys</label>
                <small>Buttplugs, dildos, and vibrators</small>
                <input
                    id="toys"
                    name="toys"
                    type="checkbox"
                    onChange={handleCheckbox}
                    value={values.toys}
                />
            </fieldset>

            <fieldset>
                <label htmlFor="positions">Position prompts</label>
                <input
                    id="positions"
                    name="positions"
                    type="checkbox"
                    onChange={handleCheckbox}
                    value={values.positions}
                />
            </fieldset>

            <fieldset>
                <label htmlFor="bonus">Bonus round</label>
                <input
                    id="bonus"
                    name="bonus"
                    type="checkbox"
                    onChange={handleCheckbox}
                    value={values.bonus}
                />
            </fieldset>

            <button type="submit">Save settings</button>
            </Stack>
        </form>
    );
};

export default ConfigForm;
