import { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { onSpin } from '../scripts';
import ConfigForm from './ConfigForm';
import Display from './Display';
import { Box } from './primitives/primitives';

const Main = () => {
    const [configuration, setConfiguration] = useState({
        first_player: 'Becky',
        total_rounds: 20,
        rounds_foreplay: 5,
        anal: false,
        hardcore: false,
        rounds_hardcore: 0,
        toys: false,
        positions: false,
        bonus: false
    });
    const pullLever = onSpin(configuration);

    return (
        <main>
            <Box>
                <Router>
                    <Routes>
                        <Route 
                            exact 
                            path="/" 
                            element={
                                <ConfigForm setConfiguration={setConfiguration} />
                            } 
                        />
                        <Route 
                            path="/play" 
                            element={
                                <Display configuration={configuration} pullLever={pullLever} />
                            } 
                        />
                    </Routes>
                </Router>
            </Box>
        </main>
    );
};

export default Main;
