import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Center, Cover, Stack } from './primitives/primitives';
import DisplayPosition from './DisplayPosition';
import DisplayString from './DisplayString';
import DisplayStage from './DisplayStage';
import DisplayCounter from './DisplayCounter';
import Button from './Button';

const Display = ({ configuration, pullLever }) => {
    const navigate = useNavigate();
    const [counter, setCounter] = useState(0);
    const [stage, setStage] = useState('');
    const [string, setString] = useState('');
    const [position, setPosition] = useState('');

    useEffect(() => {
        if (counter >= configuration.total_rounds && !configuration.bonus) {
            console.log('RESET');
            navigate('/');
        } else if (counter > configuration.total_rounds && configuration.bonus) {
            setCounter((current) => current--);
        }
    }, [counter, configuration, navigate]);

    const handleClick = () => {
        const { counter, stage, string, position } = pullLever();
        setCounter(counter);
        setString(string);
        setStage(stage);
        setPosition(position);
    };

    return (
        <section>
            <Stack space="var(--space-l)" splitAfter={4}>
                <Center>
                    <DisplayStage stage={stage} />
                </Center>

                <Center>
                    <DisplayCounter counter={counter} />
                </Center>
                
                <Center andText>
                    <DisplayPosition position={position} />
                </Center>
                
                <Center andText>
                    <DisplayString string={string} />
                </Center>
                
                <Center andText>
                    <Button variant="primary" clickHandler={handleClick}>Spin</Button>
                </Center>
            </Stack>
        </section>
    );
};

export default Display;
