import spritesheet from '../assets/icons/sprites.svg';

const DisplayPosition = ({ position }) => (
    <figure>
        <svg width="100%" style={{ maxInlineSize: '100%' }} viewBox="25 0 650 600" preserveAspectRatio="xMinYMin">
            <use href={`${spritesheet}#img-${position}`}></use>
        </svg>
        <figcaption className="position">{position.replaceAll('-', ' ').toUpperCase()}</figcaption>
    </figure>

);

export default DisplayPosition;
